.entry-page {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
  background: linear-gradient(to bottom right, #007bff, #00bfff); /* Blue gradient background */
  font-family: 'Arial', sans-serif; /* Professional font */
}

.company-logo {
  width: 300px; /* Increased logo size */
  height: 300px; /* Increased logo size */
  object-fit: contain; /* Ensures the logo fits within specified dimensions */
  border-radius: 50%; /* Makes the logo more attractive with a circular shape */
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* Adds a subtle shadow effect */
  animation: slide-in 2s ease-in-out forwards; /* Animation definition for logo */
}

.entrh1 {
  margin-top: 20px;
  font-size: 4rem; /* Increased font size */
  color: #ffffff; /* White text color against the blue gradient background */
  text-align: center; /* Center-align the heading */
  animation: zoom-in 2s ease-in-out forwards; /* Animation definition for title */
   /* Delay title animation to start after logo finishes */
}

/* Animation definition for the logo */
@keyframes slide-in {
  from {
    transform: translateX(-100vw); /* Start from off-screen left */
  }
  to {
    transform: translateX(0); /* Move to the center */
  }
}

/* Animation definition for the title */
@keyframes zoom-in {
  from {
    transform: scale(0.8); /* Start at 80% size */
  }
  to {
    transform: scale(1); /* Zoom to full size */
  }
}
